import { graphColors } from "..";
import Main from "../../../scenes/Main";

export const createMask = (scene: Main, line: Phaser.GameObjects.Graphics) => {
  const canvasWidth = Number(scene.game.config.width),
    canvasHeight = Number(scene.game.config.height);

  const square = new Phaser.Geom.Rectangle(0, 0, canvasWidth, canvasHeight);
  const geom = line.createGeometryMask();
  const background = scene.add.graphics().fillRectShape(square).setMask(geom);

  background.clear();
  background.fillGradientStyle(
    graphColors[scene.state.engine.theme].down,
    graphColors[scene.state.engine.theme].up,
    graphColors[scene.state.engine.theme].down,
    graphColors[scene.state.engine.theme].up,
  );
  background.fillRect(0, 0, canvasWidth, canvasHeight);
  return {
    geom,
    background,
  };
};
