import React, {
  useRef,
  useState,
  useEffect,
  FC,
  Suspense,
  CSSProperties,
  useLayoutEffect,
} from "react";

import {
  crashSocket,
  createTimeouts,
  isKeyboardEvent,
  rangeBetween,
} from "utils";
import { useDocumentLoaded, useEventListener, useVisibility } from "hooks";
import { useChat, useOnline, useWhiteTheme, useWindowSize } from "providers";

import Loader from "config/LazyComponentLoader";
import { Lines } from "./components";
import { config } from "./game/data/config";
import css from "./crash.module.css";
const Game = React.lazy(() => Loader(import("./game/Game")));

const crashPadding = 10;

interface Props {
  isShowEffects: boolean;
}

let withoutCrashGame = false;
//@ts-ignore
window.disableCrashRender = () => {
  withoutCrashGame = !withoutCrashGame;
};

const { clearTimeouts, pushTimeout } = createTimeouts();

const Crash: FC<Props> = ({ isShowEffects }) => {
  const boxRef = useRef<HTMLDivElement | null>(null);
  const [render, setRender] = useState(false);
  const [scaleParams, setScaleParams] = useState<{
    wrapperStyles: CSSProperties;
    scale: number;
  }>();

  const isOnline = useOnline();
  const { isWhiteThemeActive } = useWhiteTheme();
  const isLoaded = useDocumentLoaded();
  const { isHidden } = useVisibility();
  const { width } = useWindowSize();
  const { isHide } = useChat();

  const calculateScaleParams = (node = boxRef.current) => {
    const { width, height } = config;
    const { offsetWidth } = node || {};

    const scale = Math.min(
      ((offsetWidth || 0) - crashPadding * 2) / Number(width),
      1,
    );

    setScaleParams({
      wrapperStyles: {
        height: rangeBetween((Number(height) || 150) * scale, 150, 500),
      },
      scale,
    });
  };

  const startResize = (time = 300) => {
    clearTimeouts();
    setRender(false);

    pushTimeout(() => {
      setRender(true);
    }, time);
  };

  useLayoutEffect(() => {
    calculateScaleParams();
    startResize(500);
  }, [
    isOnline,
    isWhiteThemeActive,
    isLoaded,
    isHidden,
    width,
    isHide,
    isShowEffects,
  ]);

  useEffect(() => {
    calculateScaleParams();
  }, [render]);

  crashSocket.errorDispatch("app_reRender", (event: any) => {
    setRender(false);

    console.log(
      `Socket is stopped.
        Reason: ${event.reason}
        Code: ${event.code}`,
    );

    const interval = setInterval(() => {
      const status = crashSocket.status();
      if (status === 1) {
        startResize();

        clearInterval(interval);
      }
    }, 50);
  });

  useEventListener("keypress", (event) => {
    if (isKeyboardEvent(event) && event.key === "*" && event.shiftKey) {
      console.log(
        "isOnline | scaleParams | render",
        isOnline,
        scaleParams,
        render,
      );
    }
  });

  return (
    <div
      ref={boxRef}
      className={css.box}
      id="crash-wrapper"
      style={scaleParams?.wrapperStyles}>
      {!withoutCrashGame && isLoaded && (
        <Suspense fallback={<div style={{ height: 100 }} />}>
          <Game
            render={isOnline && scaleParams && render}
            scale={scaleParams?.scale}
          />
        </Suspense>
      )}
      <Lines />
    </div>
  );
};

export default Crash;
