import { axesConfig } from ".";
import Main from "../../scenes/Main";
import { Plane } from "../Graph/graphTypes";

export const getAxesText = (
  value: number | string,
  plane: Plane,
  isDisable?: boolean,
) => {
  if (isDisable) {
    if (plane === "y") {
      return "1.00x";
    } else if (typeof value === "string") {
      return "";
    }
  }

  if (typeof value === "string") {
    return value;
  }

  return plane === "x"
    ? `${axesConfig.defaultTime * value}`
    : `${(axesConfig.defaultPayout * value + 1).toFixed(2)}x`;
};

const getAxesValues = (
  plane: Plane,
  scene: Main,
  iterator: number,
  value: number | string,
) => {
  const yStart = scene.parameters.yStart * 5;
  const xStart = scene.parameters.xStart;
  const gameWidth = Number(scene.game.config.width);
  const gameHeight = Number(scene.game.config.height);

  //@ts-ignore
  const coordinate = axesConfig[`${plane}Multiplier`][iterator];
  const x = plane === "x" ? gameWidth * coordinate - xStart : 0;
  const y =
    plane === "y" ? gameHeight * coordinate - yStart : gameHeight - yStart / 2;

  const text = getAxesText(
    value,
    plane,
    plane === "y" ? iterator === 0 : gameWidth < 530,
  );

  return {
    x,
    y,
    text,
  };
};

const getStartedAxesData = (
  plane: Plane,
  iterator: number,
  configValue: string | number,
  configValues: (string | number)[],
) => (plane === "y" ? configValues[iterator - 1] : configValue);

const getConfigArray = (plane: Plane) =>
  plane === "y" ? axesConfig.payoutValues : axesConfig.textValues;

export const createAxes = (plane: Plane, scene: Main) => {
  const values = getConfigArray(plane);

  return values.map((value, i, src) => {
    const axes = getStartedAxesData(plane, i, value, src);
    const { x, y, text } = getAxesValues(plane, scene, i, axes);

    const ax = scene.add.text(x, y, text, scene.state.axes.state.dotStyle);
    return ax;
  });
};

export const axesTextReset = (
  plane: Plane,
  axes: Phaser.GameObjects.Text[],
) => {
  const values = getConfigArray(plane);

  axes.forEach((axes, i) => {
    const textValue = getStartedAxesData(plane, i, values[i], values);

    const text = getAxesText(
      textValue,
      plane,
      plane === "y" ? i === 0 : window.innerWidth < 600,
    );
    axes.setText(text);
  });
};

export const axesTextSwitch = (
  axes: Phaser.GameObjects.Text[],
  hide: boolean,
) => {
  const alphaValue = hide ? 0 : 1;
  axes.forEach((value) => value.setAlpha(alphaValue));
};
