import Main from "../scenes/Main";

const defaultTimeValue = 6;

let counter = 1;

export default class PayoutText {
  scene: Main;
  isLocal: boolean;
  startedTime: number;

  textElement: HTMLTitleElement | null = document.getElementById(
    "crash-payout-text",
  ) as HTMLTitleElement;
  timeToStart = -1;
  defaultTimeValue = 6;

  constructor(scene: Main, isLocal: boolean) {
    this.scene = scene;
    this.isLocal = isLocal;
    this.startedTime = Date.now();

    if (this.textElement) {
      const list = this.textElement.classList;
      const newClass = isLocal ? "canvasTextGilroy" : "canvasTextVarela";

      if (!list.contains(newClass)) {
        list.add(newClass);
      }
    }
  }

  createPayoutText(calculatedValue: string | number, twoNumbers?: boolean) {
    const calculatedPayout = Number(calculatedValue).toFixed(2);
    if (!this.isLocal) {
      const lastAmount = twoNumbers ? 2 : 1;
      const realPayout = Number(this.scene.parameters.webGamePayout).toFixed(2);
      const firstPayoutNumbers = realPayout.slice(
        0,
        realPayout.length - lastAmount,
      );
      const lastPayoutNumber = calculatedPayout.slice(
        calculatedPayout.length - lastAmount,
        calculatedPayout.length,
      );

      return `${firstPayoutNumbers}${lastPayoutNumber}`;
    }

    return calculatedPayout;
  }

  draw() {
    if (this.textElement) {
      switch (this.scene.parameters.gameCircle) {
        case "loading":
          this.textElement.style.left = "auto";
          break;

        case "update_value":
          if (
            !this.isLocal &&
            this.scene.parameters.currentTime !== 0 &&
            !this.textElement.classList.contains("crashGameAnimation")
          ) {
            //text animation settings
            this.textElement.style.animationDelay = `-${this.scene.parameters.currentTime}ms`;
            this.textElement.classList.add("crashGameAnimation");
          }
          if (this.timeToStart !== defaultTimeValue) {
            this.timeToStart = defaultTimeValue;
          }

          let textUpdate = "";
          if (
            !this.isLocal &&
            Number(this.scene.parameters.webGamePayout) < 3
          ) {
            textUpdate = `${this.scene.parameters.webGamePayout}x`;
          } else {
            textUpdate = `${this.createPayoutText(
              this.scene.parameters.calculatedGamePayout,
              !this.isLocal && Number(this.scene.parameters.webGamePayout) > 30,
            )}x`;
          }

          if (this.textElement.innerHTML !== textUpdate) {
            this.textElement.innerHTML = textUpdate;
          }

          break;

        case "crashed":
          counter = 0;
          const text = `Crashed @ ${
            !this.isLocal
              ? this.scene.parameters.webGamePayout
              : this.scene.parameters.calculatedGamePayout.toFixed(2)
          }x`;
          if (this.textElement.innerHTML !== text) {
            this.textElement.innerHTML = text;
          }
          this.timeToStart = -1;
          this.textElement.classList.remove("crashGameAnimation");
          this.textElement.style.left = "auto";
          break;

        case "new_round":
          this.scene.parameters.calculatedGamePayout = 1;

          this.timeToStart = this.isLocal
            ? Date.now() - this.startedTime
            : Date.now() - this.scene.state.socket.state.newRoundTime;

          const startTextValue = !this.isLocal
            ? ++counter * 0.11
            : Math.abs(
                Number(this.defaultTimeValue / 2 - this.timeToStart / 1000),
              );

          const startText = `Starts in ${this.createPayoutText(
            startTextValue,
          )}`;

          if (this.timeToStart < 0) {
            this.timeToStart = 0;
          }

          if (this.textElement.innerHTML !== startText)
            this.textElement.innerHTML = startText;

          break;

        default:
          break;
      }

      if (this.isLocal) {
        // text alignment for /crash-promo

        const elementLength =
          this.textElement.innerHTML.match(/[^\s]/g)?.length || 0;

        const elementFontSize = parseFloat(
          window
            .getComputedStyle(this.textElement)
            .getPropertyValue("font-size"),
        );

        const letterSize = elementFontSize / 2;

        let width = elementLength ? (elementLength / 2) * letterSize : 0;

        if (this.timeToStart <= 0.1 && this.timeToStart !== -1) {
          width = 0;
          this.textElement.style.transform = `translateX(-50%)`;
          this.textElement.style.left = "";
        } else {
          this.textElement.style.transform = ``;
        }

        this.textElement.style.left = `calc(50% - ${width}px)`;
      }
    }
  }
}
