import { track } from "utils";
import { isDevelop } from "utils/isDevelopment";
import Main from "../../scenes/Main";
import { calculateXY } from "./calculates/coordinates";
import { setHeadAngle } from "./calculates/setHeadAngle";
import { createGraph } from "./create/graph";
import { GraphElement, Mask } from "./graphTypes";

const isDev = isDevelop();

export const graphColors = {
  white: {
    down: 0x0083ff,
    up: 0xff7e77,
  },
  dark: {
    down: 0xf707bf,
    up: 0x01f1ff,
  },
  width: 3,
};

export default class Graph {
  scene: Main;
  graph: GraphElement;
  mask: Mask;
  dots: Phaser.GameObjects.Arc[];

  currentGraphPosition: [number, number] = [0, 0]; /*x, y */
  deactivated = false;

  constructor(scene: Main) {
    const { graph, mask, dots } = createGraph(
      scene,
      // isDev, // for show graph dots
    );
    this.graph = graph;
    this.mask = mask;
    this.dots = dots;

    this.scene = scene;
  }

  draw() {
    if (this.scene.parameters.calculatedGamePayout > 100 && this.deactivated) {
      return;
    }
    const { x, y, dots } = calculateXY(this.scene);
    this.currentGraphPosition = [x, y];

    this.graph.curveBezier.p1 = new Phaser.Math.Vector2(
      dots.dot1.x,
      dots.dot1.y,
    );
    this.graph.curveBezier.p2 = new Phaser.Math.Vector2(
      dots.dot2.x,
      dots.dot2.y,
    );
    this.graph.curveBezier.p3 = new Phaser.Math.Vector2(x, y);
    this.graph.line.clear();
    this.graph.line.lineStyle(graphColors.width, graphColors.dark.up);
    this.graph.curveBezier.draw(this.graph.line);
    if (isDev && this.dots.length) {
      try {
        if (this.dots[0]) {
          this.dots[0].x = this.graph.curveBezier.p1.x;
          this.dots[0].y = this.graph.curveBezier.p1.y;
        }
        if (this.dots[1]) {
          this.dots[1].x = this.graph.curveBezier.p2.x;
          this.dots[1].y = this.graph.curveBezier.p2.y;
        }
        if (this.dots[2]) {
          this.dots[2].x = dots.dot3.x;
          this.dots[2].y = dots.dot3.y;
        }
      } catch (error) {
        console.error("curveBezier dots: ", dots, this.dots);
        track()?.errors(error, {
          custom: {
            place: "crash graph error",
            calculatedDots: JSON.stringify(dots),
            gameDots: JSON.stringify(this.dots),
          },
        });
      }
    }

    setHeadAngle(
      this.scene,
      this.graph.head,
      this.currentGraphPosition,
      dots.dot3,
    );
  }

  hide() {
    this.deactivated = true;

    this.graph.head.setAlpha(0);
    this.mask.background.setAlpha(0);
    this.graph.line.setAlpha(0);
  }

  show() {
    this.deactivated = false;

    this.graph.head.setAlpha(1);
    this.mask.background.setAlpha(1);
    this.graph.line.setAlpha(1);
  }
}
