import React from "react";

import { repeat } from "utils";
import css from "./style.module.scss";

export const Lines = () => {
  return (
    <section className={css.lines}>
      <div className={css.linesBox}>
        {repeat(11, (i) => (
          <div className={css.line} key={`lineNumber_${i}`} />
        ))}
      </div>
    </section>
  );
};
