import Main from "../../scenes/Main";
import { AxesConfig, AxesTypes } from "../../types";
import { axesTextReset, axesTextSwitch, createAxes } from "./calculates";

const axesColors = {
  white: "#555476",
  dark: "#bd9cff",
};

const calcTime = (time: number) => {
  return time % 2 === 0;
};

export const axesConfig: AxesConfig = {
  payoutValues: [0.2, 0.4, 0.6, 0.9, 1],
  textValues: [1, 0.8, 0.6, 0.4, 0.2, "0"],
  xMultiplier: [0.966, 0.83, 0.664, 0.498, 0.332, 0.166],
  yMultiplier: [1, 0.8, 0.6, 0.4, 0.2],
  defaultPayout: 1,
  defaultTime: 10,
};

export default class Axes {
  scene: Main;
  axes: AxesTypes = {
    x: [],
    y: [],
  };

  state = {
    payoutSeparation: 0,
    dotStyle: {
      fontFamily: "Gilroy, sans-serif",
      fontWeight: "800",
      fontSize: "20px",
      color: axesColors.dark,
    },
    millisecondsSeparation: 0,
  };

  deactivated = false;

  constructor(scene: Main) {
    this.scene = scene;
  }

  create() {
    this.axes.y = createAxes("y", this.scene);
    this.axes.x = createAxes("x", this.scene);
  }

  draw(timestamp: number) {
    let payout = this.scene.parameters.calculatedGamePayout - 1;

    if (
      this.state.dotStyle.color !== axesColors[this.scene.state.engine.theme]
    ) {
      this.state.dotStyle.color = axesColors[this.scene.state.engine.theme];

      for (const key in this.axes) {
        //@ts-ignore
        const axes: Phaser.GameObjects.Text[] = this.axes[key];
        axes?.forEach((text) => {
          text.setColor(this.state.dotStyle.color);
        });
      }
    }

    if (this.deactivated || payout < 1 || calcTime(Math.round(timestamp))) {
      return;
    }

    this.axes.y.forEach((text, i) => {
      let payoutValue = axesConfig.payoutValues[i];

      if (typeof payoutValue === "number") {
        switch (i) {
          case 0:
            payoutValue *= 0.3;
            break;

          case 1:
            payoutValue *= 0.7;
            break;

          default:
            break;
        }
      }

      if (typeof payoutValue === "number") {
        text.setText(`${(payout * payoutValue + 1).toFixed(2)}x`);
      }
    });

    let time = this.scene.parameters.currentTime / 1000;
    if (time > 10) {
      this.axes.x.forEach((text, i) => {
        const payoutValue = axesConfig.textValues[i];
        if (typeof payoutValue === "number") {
          text.setText((time * payoutValue).toFixed(0));
        }
      });
    }
  }

  hide() {
    this.deactivated = true;
    axesTextReset("y", this.axes.y);
    axesTextReset("x", this.axes.x);
    axesTextSwitch(this.axes.y, true);
    axesTextSwitch(this.axes.x, true);
  }
  show() {
    this.deactivated = false;
    axesTextSwitch(this.axes.y, false);
    axesTextSwitch(this.axes.x, false);
  }
}
