import Main from "../../../scenes/Main";
import { crashGraphFixMultiplier } from "../create/graph";
import { Plane } from "../graphTypes";

const calculateDots = (scene: Main, xValue: number, yValue: number) => {
  const { height, width } = scene.game.config;

  const payout = scene.parameters.calculatedGamePayout - 1;
  const dot1XMultiplier = 0.4;
  const dot2XMultiplier = 0.75;
  const dot1YMultiplier = 0.15;
  const dot2YMultiplier = 0.35;

  const calculatePosition = (
    plane: Plane,
    multiplier: number,
    curveValue?: number,
    curve?: Phaser.Curves.CubicBezier,
  ) => {
    const value = plane === "x" ? xValue : yValue;

    const positionValue = plane === "x" ? xValue : yValue;
    const getPosition = (restoreRate?: number) => {
      let restoreValue = 0;

      if (restoreRate) {
        restoreValue = payout * (multiplier / restoreRate);
      }

      if (plane === "x") {
        const startedPosition = positionValue * (1 - payout);
        const multiplierPosition = positionValue * multiplier;

        return startedPosition > multiplierPosition
          ? startedPosition
          : multiplierPosition + restoreValue;
      }

      const startedPosition =
        Number(height) - (Number(height) - positionValue) * (1 - payout * 2);
      const multiplierPosition =
        Number(height) - (Number(height) - positionValue) * multiplier;

      return startedPosition < multiplierPosition
        ? startedPosition
        : multiplierPosition + restoreValue;
    };

    if (payout < 1 || !curveValue) {
      const position = getPosition();

      return position;
    }
    const { yStart } = scene.parameters;
    const plotStartY = yStart * crashGraphFixMultiplier.y;
    const decelerationRate = plane === "x" ? 100 : 50;
    const edge = plane === "x" ? value : Number(height) - plotStartY;
    let position = curveValue + payout * (multiplier / decelerationRate);
    if (curve && curveValue === curve.p0[plane]) {
      //restore value
      position = getPosition(decelerationRate);
    }
    if (position >= edge) {
      position = edge;
    }
    return position;
  };

  const curve = scene.state.graph?.graph.curveBezier;

  const yDot1 = calculatePosition("y", dot1YMultiplier, curve?.p1.y, curve);
  const xDot1 = calculatePosition("x", dot1XMultiplier, curve?.p1.x, curve);

  const yDot2 = calculatePosition("y", dot2YMultiplier, curve?.p2.y, curve);
  const xDot2 = calculatePosition("x", dot2XMultiplier, curve?.p2.x, curve);

  const xDot3 = xDot2;
  const yDot3 = yDot2;

  return {
    dot1: {
      x: xDot1,
      y: yDot1,
    },
    dot2: {
      x: xDot2,
      y: yDot2,
    },
    dot3: {
      x: xDot3,
      y: yDot3,
    },
  };
};

export const calculateXY = (scene: Main) => {
  const payout = scene.parameters.calculatedGamePayout - 1;

  const { yStart, xStart } = scene.parameters;
  const { height, width } = scene.game.config;

  const plotStartX = xStart * crashGraphFixMultiplier.x,
    plotStartY = yStart * crashGraphFixMultiplier.y,
    plotWidth = Number(width) - plotStartX,
    plotHeight = Number(height) - plotStartY;

  const xValue =
    payout < 1
      ? plotWidth * payout + plotStartX - xStart * 0.5
      : Number(width) - xStart * 0.5;
  const calcYValue =
    payout < 1 ? plotHeight * payout + plotStartY : Number(height);
  const yValue = Number(height) - calcYValue + yStart;

  const dots = calculateDots(scene, xValue, yValue);

  return {
    x: xValue,
    y: yValue,

    dots,
  };
};
