import { crashSocketsList } from "pages/Crash/crashSocketWatcher";
import { between } from "utils";
import { isDevelop } from "utils/isDevelopment";

import Main from "../../scenes/Main";
import calculateCashoutButton from "./calculateCashoutButton";
import { GameCircleListenerTypes } from "./listenerTypes";

const isDevelopment = isDevelop();

export default class Listener {
  scene: Main;
  isLocal: boolean;
  socketStarted = false;
  isTimeSet = false;

  state = {
    currentTime: -1,
    currentValue: 1,
    newRoundTime: -1,
    startedTime: 0,
    process: "update_value",
    path: window.location.pathname,
  };

  between = isDevelopment ? 9500 : between(7000, 10000) / 100; // between 80 and 100

  constructor(scene: Main, isLocal: boolean) {
    this.scene = scene;
    this.isLocal = isLocal;
  }

  listener() {
    if (this.isLocal) {
      // Local logic
      if (Number(3 - this.scene.state.payoutText.timeToStart / 1000) <= 0) {
        this.scene.parameters.gameCircle = "update_value";
        this.scene.state.payoutText.timeToStart = -1;
        this.state.startedTime = this.scene.savedTime = Date.now();
      }

      if (this.scene.parameters.gameCircle === "update_value") {
        if (this.scene.parameters.calculatedGamePayout >= this.between) {
          this.scene.state.payoutText.timeToStart = -1;
          this.state.currentValue = this.between;
          this.scene.parameters.gameCircle = "crashed";
        }
      }
    }

    switch (this.scene.parameters.gameCircle) {
      case "crashed":
        this.scene.parameters.calculatedGamePayout = this.state.currentValue;
        this.state.newRoundTime = -1;
        if (!this.scene.parameters.isGameHidden) {
          this.scene.crashed();
        }
        break;

      case "update_value":
        this.scene.savedTime = this.state.startedTime;
        if (
          this.scene.parameters.isGameHidden &&
          this.state.currentValue >= 1.01
        ) {
          this.scene.restart();
        }

        calculateCashoutButton(Number(this.scene.parameters.webGamePayout));
        break;

      case "new_round":
        if (!this.scene.parameters.isGameHidden) {
          this.scene.crashed();
        }
        break;

      case "start_game":
        break;

      default:
        break;
    }
  }

  getMessages() {
    if (!this.isLocal) {
      const gameCircleListener = (data: GameCircleListenerTypes) => {
        switch (data.method) {
          case "update_value":
            if (this.scene.parameters.gameCircle !== data.method) {
              this.scene.parameters.gameCircle = data.method;
              this.state.currentTime = Number(data.actualGameTime);
              this.state.currentValue = Number(data.value);
              this.state.startedTime = data.startedTime;
            }
            this.scene.parameters.webGamePayout = data.value;
            break;
          case "crashed":
            if (this.scene.parameters.gameCircle !== data.method) {
              this.scene.parameters.gameCircle = data.method;
              this.state.currentValue = Number(data.value);
            }
            this.scene.parameters.webGamePayout = data.value;

            break;
          case "new_round":
            if (this.scene.parameters.gameCircle !== data.method) {
              this.scene.parameters.gameCircle = data.method;

              if (this.state.newRoundTime === -1) {
                this.state.newRoundTime = data.time;
                this.scene.state.payoutText.defaultTimeValue = Number(
                  data.value,
                );
              }
            }
            this.scene.parameters.webGamePayout = data.value;
            break;

          case "start_game":
            break;

          default:
            break;
        }
      };

      crashSocketsList.gameCircleListener = gameCircleListener;
      crashSocketsList.gameUsersListener =
        this.scene.state.users.createPlayerListener();
    } else {
      this.scene.parameters.gameCircle = "new_round";
      this.state.currentValue = 6;
    }
  }
}
