import { CrashStoryListener } from "games/Crash/game/engine/entities/SocketListener/listenerTypes";
import { createTimeouts } from "utils";
import { CrashGameCircleValue } from "./controller/types";
import {
  CrashCoinParams,
  GameCircleListenerTypes,
  GameUsersListenerTypes,
  HistoryListener,
  ManagerListenerProps,
  ProfitTableListenerType,
} from "./crashTypes";

interface CrashSocketsListTypes {
  gameCircleListener: (data: GameCircleListenerTypes) => void;
  gameUsersListener: (data: GameUsersListenerTypes) => void;
  profitTableListener: (data: ProfitTableListenerType) => void;

  managerListener: (data: ManagerListenerProps) => void;
  historyListener: (data: HistoryListener) => void;
  statisticsHistoryListener: (data: CrashStoryListener) => void;
  crashMainHistoryListener: (data: CrashStoryListener) => void;

  ping: number;
}

export const crashSocketsList: CrashSocketsListTypes = {
  gameCircleListener: () => null,
  gameUsersListener: () => null,
  statisticsHistoryListener: () => null,
  crashMainHistoryListener: () => null,

  profitTableListener: () => null,
  managerListener: () => null,
  historyListener: () => null,

  ping: 0,
};

const intervals: NodeJS.Timeout[] = [];
const clearIntervals = () => {
  intervals.forEach((i) => clearInterval(i));
  intervals.length = 0;
};

const { clearTimeouts, pushTimeout } = createTimeouts();

let gameCircleValue: CrashGameCircleValue | null = null;
export const setGameCircleValue = (
  value: CrashGameCircleValue | null,
  callBack?: () => void,
) => {
  if (gameCircleValue !== value) {
    gameCircleValue = value;

    callBack && callBack();
  }
};

let pingText = document.getElementById(
  "crashPing",
) as HTMLParagraphElement | null;

const lostConnectionTime = 2000;

const showPing = () => {
  const nowPing = Date.now();
  const pingValue = Math.max(nowPing - crashSocketsList.ping - 100, 0);

  if (pingText) {
    pingText.innerText = `Ping: ${pingValue}`;

    if (pingValue <= 200) {
      pingText.style.display = "none";
    } else {
      pingText.style.display = "inline";
    }

    if (pingValue > 500) {
      pingText.style.color = "darkred";
    } else {
      pingText.style.color = "";
    }

    if (pingValue >= lostConnectionTime) {
      pingText.innerText = `Connection lost`;
    }
  } else {
    pingText = document.getElementById(
      "crashPing",
    ) as HTMLParagraphElement | null;
  }

  return pingValue;
};

export const crashSocketWatcher = (data: any) => {
  clearTimeouts();

  const getOfflinePing = (delay = 600) => {
    pushTimeout(() => {
      const ping = showPing();

      if (ping < lostConnectionTime) {
        getOfflinePing();
      } else {
        crashSocketsList.managerListener({
          method: "offline",
        });
      }
    }, delay);
  };

  if (data.method === "crashed") {
    clearTimeouts();
    getOfflinePing(5000);
  } else if (data.action === "crash_update") {
    clearIntervals();
    getOfflinePing();

    if (
      data.method !== "new_round" ||
      (data.method === "new_round" && Number(data.value) < 5.8)
    ) {
      showPing();
    }
  }

  crashSocketsList.ping = Date.now();
  crashSocketsList.managerListener({
    method: "online",
  });

  switch (data.action) {
    case "crash_update":
      switch (data.method) {
        case "crashed":
          setGameCircleValue(data.method, () => {
            crashSocketsList.managerListener({
              method: data.method,
            });
          });
          crashSocketsList.gameCircleListener({
            method: "crashed",
            value: data.value,
          });

          const interval = setInterval(() => {
            crashSocketsList.gameCircleListener({
              method: "crashed",
              value: data.value,
            });
          }, 200);
          intervals.push(interval);

          crashSocketsList.profitTableListener({
            method: "crashed",
          });
          crashSocketsList.historyListener({
            method: "crashed",
            round: {
              crash_point: data.value,
              seed: data.server_seed,
              id: data.id,
            },
          });
          const round: CrashStoryListener = {
            method: "crashed",
            round: {
              id: data.id,
              crash_point: data.value,
              seed: data.server_seed,
            },
          };
          crashSocketsList.statisticsHistoryListener(round);
          crashSocketsList.crashMainHistoryListener(round);

          break;
        case "new_round":
          setGameCircleValue(data.method, () => {
            crashSocketsList.managerListener({
              method: data.method,
            });
          });
          crashSocketsList.gameCircleListener({
            method: "new_round",
            time: Date.now() - Number(data.value) * 1000,
            value: data.value,
          });

          break;

        case "bet_cashedout":
        case "bet_played":
          crashSocketsList.managerListener({
            method: data.method,
            balance: data.balance,
            error: data.error,
          });
          break;

        case "new_bet":
          crashSocketsList.gameUsersListener({
            method: "new_bet",
            bet: {
              profit: "",
              user_id: data.user_id,
              user_name: data.user_name,
              cashed_out_at: data.cashed_out_at,
            },
          });

          crashSocketsList.profitTableListener({
            method: "new_bet",
            ...data,
          });
          break;

        case "new_bet_finalized":
          crashSocketsList.managerListener({
            method: "new_bet_finalized",
            coin: data.coin,
            profit: data.profit,
            amount: data.bet_amt,
          });
          break;

        case "start_game":
          crashSocketsList.gameCircleListener({ method: "start_game" });

          break;

        case "update_value":
          setGameCircleValue(data.method, () => {
            crashSocketsList.managerListener({
              method: data.method,
            });
          });
          crashSocketsList.gameCircleListener({
            method: "update_value",
            value: data.value,
            actualGameTime: data.time,
            startedTime: Date.now() - Number(data.time),
          });

          break;

        case "current_bets":
          const betsList: any = {
            method: "current_bets",
            bets: data.bets,
          };

          crashSocketsList.gameUsersListener(betsList);
          crashSocketsList.profitTableListener(betsList);
          crashSocketsList.managerListener(betsList);
          break;

        case "bet_cashedout_update":
          crashSocketsList.gameUsersListener({
            method: "bet_cashedout_update",
            user_id: data.user_id,
            profit: data.profit,
            at: data.at,
          });

          crashSocketsList.profitTableListener({
            method: "bet_cashedout_update",
            ...data,
          });

          crashSocketsList.managerListener({
            method: "bet_cashedout_update",
            user_id: data.user_id,
          });

          break;

        case "previous_rounds":
          crashSocketsList.historyListener({
            method: "previous_rounds",
            rounds: data.rounds,
          });
          const rounds: CrashStoryListener = {
            method: "previous_rounds",
            rounds: data.rounds,
          };
          crashSocketsList.statisticsHistoryListener(rounds);
          crashSocketsList.crashMainHistoryListener(rounds);

          break;

        default:
          break;
      }
      break;

    case "update_balance":
      break;

    case "update_coin_parameters":
      const params: CrashCoinParams = {
        ...data,
      };
      crashSocketsList.managerListener({
        method: "update_coin_parameters",
        coinPrice: params,
      });
      break;

    default:
      break;
  }

  if (data.error) {
  }
};
