import { crashGraphFixMultiplier } from "../create/graph";
import Main from "../../../scenes/Main";

interface Coordinate {
  x: number;
  y: number;
}

const outsideValues = {
  isFirstLoad: true,
};

export const setIsFirstLoad = (value: boolean) => {
  outsideValues.isFirstLoad = value;
};

export const getAngle = (d1: Coordinate, d2: Coordinate) => {
  const vector = {
    x: d1.x - d2.x,
    y: d1.y - d2.y,
  };

  return Math.atan2(vector.x, vector.y) * (180 / 3.14) - 130;
};

export const setHeadAngle = (
  scene: Main,
  head: Phaser.GameObjects.Sprite,
  positions: [number, number],
  angleDot: Coordinate,
  /*x, y*/
) => {
  const payout = scene.parameters.calculatedGamePayout;
  const headDot = {
    x: head.x,
    y: head.y,
  };

  if (payout < 1.05) {
    const { width, height } = scene.game.config;
    const { yStart, xStart } = scene.parameters;
    const plotStartY =
      Number(scene.game.config.height) -
      yStart * crashGraphFixMultiplier.y +
      yStart;
    const plotStartX = xStart * crashGraphFixMultiplier.x - xStart * 0.5;

    angleDot.x -= 0;
    angleDot.y += Number(height);

    headDot.x = Number(width) - plotStartX;
    headDot.y = plotStartY;
  }
  let newAngle = getAngle(headDot, angleDot);

  let rotateSpeed = 2;

  if (payout < 1.3 && !outsideValues.isFirstLoad) {
    rotateSpeed = 0.25;
  } else if (payout > 1.3 && outsideValues.isFirstLoad) {
    outsideValues.isFirstLoad = false;
  }

  const angleDifference = Math.floor(-newAngle - head.angle);
  if (angleDifference < -1 || angleDifference > 1) {
    if (-newAngle > head.angle) {
      head.setAngle(head.angle + rotateSpeed);
    } else if (-newAngle < head.angle) {
      head.setAngle(head.angle - rotateSpeed);
    }
  }

  head.setPosition(...positions);
  head.setDepth(100);
};
