import { track } from "utils";

export default function calculateButton(GamePayout: number) {
  const calculateButtonTextElement =
    document.querySelector(".crash-pay-button");

  try {
    if (calculateButtonTextElement) {
      const localAmt = localStorage.getItem("crashBet");

      if (localAmt) {
        const amount = JSON.parse(localAmt);
        const count =
          Number(amount.amt) * Number(GamePayout) - Number(amount.amt);

        const content = `${Number(count).toFixed(8)} ${amount.coin}`;

        calculateButtonTextElement.innerHTML = content;
      } else {
        // console.log("cashout button error localAmt: ", localAmt);
      }
    } else {
      // console.log("cashout button error button: ", calculateButtonTextElement);
    }
  } catch (error) {
    const message = `
      ${error?.stack}
      calculateButton
      localStorage: ${JSON.stringify(localStorage)}`;

    // console.log("cashout button error catch: ", message);

    try {
      track()?.errors(message);
    } catch {
      console.log(" trackError", error);
    }
  }
}
