import { graphColors } from "..";
import Main from "../../../scenes/Main";
import { createMask } from "./mask";

export const crashGraphFixMultiplier = {
  x: 1.5,
  y: 3,
};

export const createGraph = (scene: Main, showDots?: boolean) => {
  const { yStart, xStart } = scene.parameters;
  const pointers = [
    new Phaser.Math.Vector2(
      xStart * crashGraphFixMultiplier.x - xStart * 0.5,
      Number(scene.game.config.height) -
        yStart * crashGraphFixMultiplier.y +
        yStart,
    ),
    new Phaser.Math.Vector2(Number(scene.game.config.width), 0),
  ];
  const dots = [
    new Phaser.Math.Vector2(200, 200),
    new Phaser.Math.Vector2(100, 200),
  ];

  const line = scene.add.graphics();
  const curveBezier = new Phaser.Curves.CubicBezier(
    pointers[0],
    dots[0],
    dots[1],
    pointers[1],
  );

  const mask = createMask(scene, line);

  const bezierDots: Phaser.GameObjects.Arc[] = [];

  const head = scene.add
    .sprite(0, 0, "head")
    .setAngle(45)
    .setAlpha(1)
    .setDepth(3)
    .setOrigin(0.75, 0.3)
    .setTintFill(graphColors[scene.state.engine.theme].up);

  if (showDots) {
    bezierDots.push(
      scene.add.circle(curveBezier.p1.x, curveBezier.p1.y, 5, 0x00ff00),
    );
    bezierDots.push(
      scene.add.circle(curveBezier.p2.x, curveBezier.p2.y, 5, 0xffff00),
    );
    bezierDots.push(
      scene.add.circle(curveBezier.p2.x, curveBezier.p2.y, 5, 0xff00ff),
    );
  }

  return {
    graph: {
      line,
      curveBezier,
      head,
    },
    mask,
    dots: bezierDots,
  };
};
