import { CurrentBetsState } from "pages/Crash/crashTypes";
import { replaceNumbers, track } from "utils";
import Main from "../../scenes/Main";
import { PlayedUser, PlayerList, WinnersList } from "../../types";
import { GameUsersListenerTypes } from "../SocketListener/listenerTypes";

interface Winner {
  at: string | number;
  id: string | number;
}

let newWinner: Winner | null = null,
  newListenerUserList: PlayerList = {};

const addNewPlayers = (users: CurrentBetsState) => {
    newListenerUserList = {};
    for (const key in users) {
      const bet = users[key];
      if (bet) {
        newListenerUserList[bet.user_id] = {
          //@ts-ignore
          profit: bet.profit || null,
          user_id: bet.user_id,
          user_name: bet.user_name,
          cashed_out_at: bet.cashed_out_at || "",
        };
      }
    }
  },
  addNewWinner = (id: string | number, at: string | number) => {
    newWinner = {
      at,
      id,
    };
  },
  addNewPlayer = (value: PlayedUser) => {
    newListenerUserList[value.user_id] = value;
  };

export default class Users {
  scene: Main;
  listenerUserList: PlayerList = {};

  winnersUsers: WinnersList = {};

  winnerList: {
    [id: string]: Winner;
  } = {};

  userTextStyles = {
    fontSize: "0.625rem",
    color: "white",
  };

  constructor(scene: Main) {
    this.scene = scene;
  }

  watch() {
    this.playersListListener();
    this.newWinnerListener();
    this.winnersListener();

    if (this.scene.parameters.gameCircle === "crashed") {
      this.clear();
    }
  }

  clear() {
    for (const id in this.winnersUsers) {
      if (this.winnersUsers[id]) {
        this.winnersUsers[id].body?.destroy();
        delete this.winnersUsers[id];
        delete this.winnerList[id];
      }
    }

    newWinner = null;
    newListenerUserList = {};
    this.winnerList = {};
  }

  playersListListener() {
    if (newListenerUserList !== this.listenerUserList) {
      for (const id in this.listenerUserList) {
        const user = this.winnersUsers[id];
        if (user) {
          user.body?.destroy();
          delete this.winnersUsers[id];
          delete this.winnerList[id];
        }
      }

      this.listenerUserList = newListenerUserList;
    }
  }

  newWinnerListener() {
    if (newWinner) {
      this.winnerList[newWinner.id] = newWinner;
      newWinner = null;
    }
  }

  winnersListener() {
    Object.values(this.winnerList).forEach(({ at, id }) => {
      const user = this.listenerUserList[id];
      const coordinates = this.scene.state.graph?.currentGraphPosition;
      if (user && coordinates && !this.winnersUsers[id]?.body) {
        const [x, y] = coordinates;
        const { height, width } = this.scene.game.config;
        const body = this.scene.add
          .text(
            x > Number(width) / 2 ? x - 50 : x + 100,
            y,
            `${user.user_name} @${replaceNumbers(`${at}`)}x`,
            this.userTextStyles,
          )
          .setOrigin(1, 0.5);
        const tween = this.scene.tweens.add({
          targets: body,
          y: height,
          x: 100,
          alpha: 0,
          duration: 2500,
          ease: "Cubic.easeIn",
        });

        tween.on("complete", () => {
          try {
            body?.destroy();
            delete this.winnersUsers[id];
            delete this.winnerList[id];
          } catch (err) {
            const msg = `
              error on tween destroy:
              user: ${this.winnersUsers[id].user_name} /
              ${JSON.stringify(this.winnersUsers)}
            `;
            console.error(msg);

            track()?.errors(err, {
              custom: {
                errorMessage: msg,
              },
            });
          }
        });

        this.winnersUsers[id] = {
          ...user,
          y,
          body,
          tween,
        };
      }
    });
  }

  createPlayerListener() {
    return (data: GameUsersListenerTypes) => {
      switch (data.method) {
        case "current_bets":
          addNewPlayers(data.bets);
          break;

        case "bet_cashedout_update":
          addNewWinner(data.user_id, data.at);
          break;

        case "new_bet":
          addNewPlayer(data.bet);
          break;

        default:
          break;
      }
    };
  }
}
