import Main from "../../scenes/Main";
import { CalcScaleAnimation, GameCircle, VideosList } from "../../types";

let isEffectsActive = JSON.parse(
  localStorage.getItem("isShowCrashEffect") || "true",
);
export const setIsCrashEffectsActive = (value: boolean) =>
  (isEffectsActive = value);

const calcScale = (params: CalcScaleAnimation) => {
  const widthCoefficient = params.config.width / params.video.width;
  const heightCoefficient = params.config.height / params.video.height;

  return Math.max(widthCoefficient, heightCoefficient);
};

export default class Video {
  scene: Main;
  videos: VideosList = {};
  constructor(scene: Main) {
    this.scene = scene;
  }

  lazyLoading(
    name: string,
    fileName: string,
    params: {
      loop?: boolean;
    } = {
      loop: true,
    },
  ) {
    this.videos[name] = null;

    const theme = this.scene.isLocal ? "dark" : this.scene.state.engine.theme;

    const centerCoordinates = {
      x: Number(this.scene.game.config.width) / 2,
      y: Number(this.scene.game.config.height) / 2,
    };

    const videoPath = `assets/crash/video/${theme}/${fileName}`;
    const blendMode =
      theme === "dark" ? Phaser.BlendModes.SCREEN : Phaser.BlendModes.MULTIPLY;

    const loader = new Phaser.Loader.LoaderPlugin(this.scene);
    loader.video(name, videoPath);
    loader.on(Phaser.Loader.Events.COMPLETE, () => {
      if (!this.videos[name]) {
        this.videos[name] = this.scene.add
          .video(centerCoordinates.x, centerCoordinates.y, name)
          .setBlendMode(blendMode)
          .setMute(true)
          .setAlpha(0)
          .play(params.loop)
          .setPaused(false)
          .setOrigin(0.5)
          .setAlpha(0)
          .on("play", () => {
            this.videos[name]?.setAlpha(1);
          });

        const scale = calcScale({
          video: {
            height: this.videos[name]?.height || 0,
            width: this.videos[name]?.width || 0,
          },
          config: {
            height: Number(this.scene.game.config.height),
            width: Number(this.scene.game.config.width),
          },
        });

        this.videos[name]?.setScale(scale);

        if (!params.loop) {
          this.videos[name]?.on("complete", () => {
            this.videos[name]?.setPaused(true).setAlpha(0).setCurrentTime(0);
          });
        }
      }
    });

    loader.start();
  }

  listener(circle: GameCircle, payout: number) {
    if (!isEffectsActive && !this.scene.isLocal) {
      return;
    }

    const playEffectVideo = (name: string) => {
      if (
        this.videos[name] &&
        (this.videos[name]?.alpha !== 1 || !this.videos[name]?.isPlaying())
      ) {
        this.videos[name]
          ?.setCurrentTime(0)
          .setPaused(false)
          .play()
          .setAlpha(1)
          .setDepth(2);
      } else if (typeof this.videos[name] !== "object") {
        this.scene.state.videos.lazyLoading(`${name}`, `${name}.mp4`, {
          loop: false,
        });
      }
    };

    switch (circle) {
      case "update_value":
        if (
          !this.videos.loop500 &&
          this.videos.stars &&
          this.videos.stars.alpha !== 1
        ) {
          this.videos.stars
            ?.setCurrentTime(0)
            .setPaused(false)
            .setAlpha(1)
            .play(true);
        } else if (typeof this.videos.stars !== "object") {
          this.scene.state.videos.lazyLoading("stars", "stars.mp4");
        }

        const roundPayout = Number(Math.floor(payout).toFixed());

        switch (roundPayout) {
          //@ts-ignore
          case 2:
            if (payout > 2.5) {
              break;
            }
          case 5:
          case 10:
          case 20:
            playEffectVideo("p0_20");
            break;

          case 30:
          case 100:
          case 250:
          case 500:
            playEffectVideo("p25_500");

            break;

          default:
            if (payout > 500) {
              if (this.videos.loop500 && this.videos.loop500.alpha !== 1) {
                this.videos.loop500?.setPaused(false).setAlpha(1);

                if (this.videos.stars?.alpha !== 0) {
                  this.videos.stars?.setPaused(true).setAlpha(0);
                }
              } else if (typeof this.videos.loop500 !== "object") {
                this.scene.state.videos.lazyLoading("loop500", "loop500.mp4");
              }

              if (payout > 1000) {
                if (
                  (payout > 1000 && payout < 1010) ||
                  (payout > 5000 && payout < 5100) ||
                  payout > 8000
                ) {
                  playEffectVideo("p1000_");
                }
              }
            }
            break;
        }
        break;

      default:
      case "crashed":
      case "new_round":
        if (this.videos.stars?.alpha !== 0) {
          this.videos.stars?.setAlpha(0).setCurrentTime(0).setPaused(true);
        }

        if (this.videos.loop500?.alpha !== 0) {
          this.videos.loop500
            ?.setAlpha(0)
            .setCurrentTime(0)
            .setPaused(true)
            .destroy();
          delete this.videos.loop500;
        }

        if (this.videos.p0_20?.alpha !== 0) {
          this.videos.p0_20?.setAlpha(0).setCurrentTime(0).setPaused(true);
        }

        if (this.videos.p25_500?.alpha !== 0) {
          this.videos.p25_500
            ?.setAlpha(0)
            .setCurrentTime(0)
            .setPaused(true)
            .destroy();
          delete this.videos.p25_500;
        }

        if (this.videos.p1000_?.alpha !== 0) {
          this.videos.p1000_
            ?.setAlpha(0)
            .setCurrentTime(0)
            .setPaused(true)
            .destroy();
          delete this.videos.p1000_;
        }
        break;
    }
  }
}
