import objectsAdd from "./modules/loader/objectsAdd";
import { preloadData } from "./modules/loader/preloadData";
import Main from "./scenes/Main";
import { Loader, SimpleObject } from "./types";

export default class Engine {
  scene: Main;
  loader: Loader;
  objects: SimpleObject;
  fpsDomObject?: Element | null;

  calculateValues: SimpleObject = {
    YAxisSizeMultiplier: 2,
    XAxisPlotMinValue: 10000,
  };
  theme: "white" | "dark" = "dark";
  onlineStatus: "online" | "offline" = "online";

  constructor(scene: Main) {
    this.scene = scene;
    this.objects = {};
    this.loader = {
      preload: preloadData.bind(this.scene),
      addObjects: objectsAdd.bind(this.objects),
    };

    const colorScheme = localStorage.getItem("color-scheme");

    if (colorScheme === "light") {
      this.theme = "white";
    } else {
      this.theme = "dark";
    }
  }

  showFps(disable = false) {
    if (disable) {
      return;
    }

    const dom = this.fpsDomObject;

    if (!dom) {
      this.fpsDomObject = document.querySelector("#crashFPS");
    } else {
      let fps = this.scene.game.loop.actualFps;
      dom.innerHTML = fps.toFixed(1);
    }
  }

  calcGamePayout(t: number, isLocal?: boolean) {
    let payout = (100 * Math.exp(0.00006 * t)) / 100;
    if (payout >= 1.01) {
      this.scene.restart();
    }

    const webPayout = Number(this.scene.parameters.webGamePayout);

    if (payout >= 1.01 && payout - webPayout > 0.5) {
      payout = !isLocal ? webPayout : payout;
    }

    return payout;
  }
}
