import { Scene } from "phaser";

import Axes from "../entities/Axes";
import Graph from "../entities/Graph";
import { GamePages } from "../entities/Graph/graphTypes";
import PayoutText from "../entities/PayoutText";
import SocketListener from "../entities/SocketListener";
import Users from "../entities/Users";

import addObjectsConfig from "../datas/addObjects";
import preloadData from "../datas/preloadData";
import { MainParameters, SceneState } from "../types";
import Engine from "..";
import Video from "../entities/Video";

const startY = 10,
  startX = 40;
const isDevelopment = process.env.NODE_ENV === "development";

export default class Main extends Scene {
  state: SceneState;
  savedTime = -1;

  parameters: MainParameters = {
    plotHeight: startY,
    plotWidth: startX,
    xStart: startX,
    yStart: startY,
    gameCircle: "loading",

    calculatedGamePayout: 1,
    webGamePayout: 0,

    heightIncrement: 0,
    currentTime: 0,
    widthIncrement: 0,

    isGameHidden: false,
  };

  isLocal = window.location.pathname === GamePages.promo;

  constructor(config: string) {
    super(config);

    this.state = {
      engine: new Engine(this),
      payoutText: new PayoutText(this, this.isLocal),
      axes: new Axes(this),
      socket: new SocketListener(this, this.isLocal),
      users: new Users(this),
      videos: new Video(this),
    };
  }

  preload() {
    this.parameters.plotHeight = Number(this.game.config.height) - startY;
    this.parameters.plotWidth = Number(this.game.config.width) - startX;

    this.state.engine.loader.preload(preloadData);

    this.state.axes.create();
    this.state.axes.hide();
  }
  create() {
    this.state.graph = new Graph(this);
    this.state.socket.getMessages();

    this.crashed();

    const canvas = this.game.canvas;
    canvas.style.opacity = "1";
  }
  update(time: number, delta: number) {
    this.state.videos.listener(
      this.parameters.gameCircle,
      this.parameters.calculatedGamePayout,
    );
    this.state.graph?.draw();
    if (isDevelopment) {
      this.state.engine.showFps();
    }

    this.state.axes?.draw(time);
    this.state.payoutText.draw();

    this.timeCalculate();
    this.parameters.calculatedGamePayout = this.state.engine.calcGamePayout(
      this.parameters.currentTime,
      this.isLocal,
    );

    this.state.socket.listener();
    this.state.users.watch();
  }

  timeCalculate() {
    if (this.parameters.gameCircle !== "update_value") {
      this.savedTime = -1;
      return;
    }
    if (this.savedTime > 0) {
      const actualTime = Date.now();
      let currentGameTime = actualTime - this.savedTime;
      this.parameters.currentTime = currentGameTime;
    } else {
      if (this.savedTime === 0) this.savedTime = Date.now();
    }
  }

  crashed() {
    this.state.graph?.hide();
    this.savedTime = -1;
    this.parameters.currentTime = 0;
    this.state.axes?.hide();

    this.parameters.isGameHidden = true;
  }

  restart() {
    this.state.graph?.show();
    this.state.axes?.show();

    this.parameters.isGameHidden = false;
  }
}
